.news {
    @media screen and (min-width: 768px) {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
    }
    &-left {
        @media screen and (min-width: 768px) {
            width: 65%;
        }
    }

    &-right {
        @media screen and (min-width: 768px) {
            width: 35%;
        }
    }
}

.new {
    border-bottom: 1px solid #bdbdbd;
    padding: 0 0 16px;
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
        border-bottom: none;
        display: flex;
        flex-flow: row nowrap;
    }

    &-picture {
        margin-bottom: 16px;
        width: 100%;

        img {
            display: block;
            margin: auto;
        }
    }

    &-picture--small {
        display: none;
        @media screen and (min-width: 768px) {
            display: flex;
            flex-flow: row nowrap;
            width: 170px;
        }
    }

    &-content--small {
        @media screen and (min-width: 768px) {
            padding: 0 32px;
        }
    }

    &-content {
        @media screen and (min-width: 768px) {
            padding: 0 32px;
        }
    }

    &-title {
        font-size: 18px;
        font-weight: 900;
        line-height: 22px;
        margin: 16px 0 24px;
        color: #333;

        @media screen and (min-width: 768px) {
            font-size: 22px;
        }
    }

    &-datetime {
        font-size: 10px;
        letter-spacing: 1px;
        color: #9e9e9e;

        @media screen and (min-width: 768px) {
            font-size: 12px;
        }

        &::before {
            border-top: 1px solid $separatorLine;
            bottom: 4px;
            content: "";
            display: block;
            position: relative;
            width: 16px;
        }
    }
}
